import React, { useState, useEffect } from 'react';
import C1GeneralInfos from './components/c1-general-infos';
import C3Diagram from './components/c3-diagram';
import H1ResponsiveC1 from './components/h1-responsive-c1';

function App() {
  // If your handshake has more steps, adjust this max index.
  // Example: if you have 10 steps (0..9), set lastStep = 9.
  const lastStep = 11; // or 9, etc.

  const [currentStep, setCurrentStep] = useState(-1);
  const [visibleSteps, setVisibleSteps] = useState([]);
  const [isSmallScreen, setIsSmallScreen] = useState(window.innerWidth < 768);

  // Handle responsive layout
  useEffect(() => {
    const handleResize = () => setIsSmallScreen(window.innerWidth < 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Go to next step
  const handleNext = () => {
    const nextStep = currentStep + 1; // from -1 -> 0 -> 1 -> ...
    if (nextStep <= lastStep) {
      setCurrentStep(nextStep);
      setVisibleSteps((prev) => [...prev, nextStep]);
    }
  };

  // Reset diagram
  const handleReset = () => {
    setCurrentStep(-1);
    setVisibleSteps([]);
  };

  // Go back one step (optional)
  const handlePrev = () => {
    if (currentStep > -1) {
      const prevStep = currentStep - 1;
      setCurrentStep(prevStep);
      // Remove last step from visible set
      setVisibleSteps((prev) => prev.slice(0, -1));
    }
  };

  return (
    <div className="min-h-screen w-full bg-gray-900 text-white overflow-y-auto scrollbar-none">
      {isSmallScreen ? (
        /* ---------- Mobile Layout ---------- */
        <div className="flex flex-col w-full min-h-screen">
          {/* Top header replaces C1 on small screens */}
          <div className="w-full bg-gray-800 p-4">
            <H1ResponsiveC1
              currentStep={currentStep}
              onNext={handleNext}
              onReset={handleReset}
              onPrev={handlePrev}
              visibleSteps={visibleSteps}
            />
          </div>
          {/* Full width diagram below */}
          <div className="flex flex-1 w-full">
            <div className="w-full min-h-full">
              <C3Diagram currentStep={currentStep} visibleSteps={visibleSteps} />
            </div>
          </div>
        </div>
      ) : (
        /* ---------- Desktop Layout ---------- */
        <div className="flex w-full min-h-screen">
          {/* Column 1: c1-general-infos */}
          <div className="w-[23%]">
            <C1GeneralInfos
              currentStep={currentStep}
              onNext={handleNext}
              onReset={handleReset}
              onPrev={handlePrev}
              visibleSteps={visibleSteps}
            />
          </div>
          {/* Column 2: c3-diagram takes the rest */}
          <div className="w-[77%] min-h-full">
            <C3Diagram currentStep={currentStep} visibleSteps={visibleSteps} />
          </div>
        </div>
      )}
    </div>
  );
}

export default App;