import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';
import ReactDOMServer from 'react-dom/server';
// Example icon imports (you can customize or add more as needed):
import { IconKeys, IconCertificate, IconEncrypted, IconClearText } from './Icons';
import { globalFontSizes } from '../utils/globalFontSizes';

/*
  Four vertical lines (Client, DNS, Client-Facing Server (CFS), Backend).
  Steps 0..1 => DNS
  Steps 2..9 => ECH flow
*/

const C3Diagram = ({ currentStep, visibleSteps }) => {
  const svgRef = useRef();
  const diagramRef = useRef({});
  const drawnStepsRef = useRef(new Set());
  const [totalHeight, setTotalHeight] = useState(0);

  // Convert rem->px
  const remToPx = (rem) =>
    rem * parseFloat(getComputedStyle(document.documentElement).fontSize);

  // Additional space for top banner
  const topBannerHeight = remToPx(6);

  // Position each step vertically
  const getStepY = (index) => {
    // Matches c1/c2/c4 logic: 9rem header, 4rem topMargin, plus topBannerHeight
    const headerHeight = remToPx(9);
    const topMargin = remToPx(4);
    const baseSpacing = remToPx(12);

    let position = headerHeight + topMargin + topBannerHeight;
    for (let i = 0; i < index; i++) {
      position += baseSpacing;
    }
    return position;
  };

  // The extended flow with DNS steps (0..1) + ECH steps (2..9) = 10 total
  // Example: we color field "Encrypted Inner CH" in step #2 with specialColor = "#ef4444"
  const steps = [
    {
      id: 0,
      name: "Publish ECH DNS Record",
      icon: <IconKeys className="text-blue-400" />,
      from: "cfs", // client-facing server
      to: "dns",
      fields: [
        {
          name: "ECH Config Key",
          tooltip: "CFS uploads ECH key + parameters\n(DNS record dns65 or similar)"
        },
        {
          name: "Valid For Domain",
          tooltip: "ECH config is published for example.com\nand must be kept up-to-date"
        }
      ]
    },
    {
      id: 1,
      name: "DNS Resolve ECH",
      icon: <IconClearText className="text-orange-400" />,
      from: "client",
      to: "dns",
      fields: [
        {
          name: "DNS Query",
          tooltip: "Client requests domain's ECH record\nDNS type: e.g. SVCB or custom"
        },
        {
          name: "DNS Reply",
          tooltip: "DNS returns ECH config + IP\nClient obtains needed public key"
        }
      ]
    },
    {
      id: 2,
      name: "DNS Response",
      icon: <IconKeys className="text-blue-400" />,
      from: "dns", // client-facing server
      to: "client",
      fields: [
        {
          name: "DNS 65 record",
          tooltip: "CFS  ECH key + parameters\n(DNS record dns65 or similar)"
        },
        {
          name: "Valid For Domain",
          tooltip: "ECH config is published for example.com\nand must be kept up-to-date"
        }
      ]
    },
    {
      id: 3,
      name: "ClientHello (Outer)",
      icon: <IconKeys className="text-blue-400" />,
      from: "client",
      to: "cfs",
      fields: [
        { 
          name: "Outer SNI", 
          tooltip: "Fallback/Public SNI\n(e.g. public.example.net)" 
        },
        { 
          name: "Encrypted Inner CH", 
          tooltip: "Contains real SNI = gmail.com", 
          specialColor: "#ef2224" // Example: specialColor usage
        }
      ]
    },
    {
      id: 4,
      name: "Decrypt & Forward CH",
      icon: <IconEncrypted className="text-orange-400" />,
      from: "cfs",
      to: "backend",
      fields: [
        { name: "Decrypt success", tooltip: "Front server extracts real SNI" },
        { name: "Proxies to backend", tooltip: "Forward the inner ClientHello to the real server" }
      ]
    },
    {
      id: 5,
      name: "ServerHello",
      icon: <IconKeys className="text-blue-400" />,
      from: "backend",
      to: "client",
      fields: [
        { name: "Chosen Cipher", tooltip: "Server picks a cipher suite" },
        { name: "Key Share", tooltip: "Server ephemeral key" }
      ]
    },
    {
      id: 6,
      name: "Server Certificate",
      icon: <IconCertificate className="text-green-400" />,
      from: "backend",
      to: "client",
      fields: [
        { 
          name: "Common Name (CN)", 
          tooltip: "The domain name this certificate is issued for (e.g., example.com), this is used by some NGN against SNI for extra security", 
          specialColor: "#08EE45" 
        },
        { 
          name: "Version", 
          tooltip: "Specifies the X.509 certificate version (e.g., v3)" 
        },
        { 
          name: "Issuer", 
          tooltip: "The authority that issued this certificate (e.g., Let's Encrypt, DigiCert)" 
        },
        { 
          name: "Validity", 
          tooltip: "The start date to end date of the certificate's validity period" 
        },
        { 
          name: "Sig/Keys Algos", 
          tooltip: "The cryptographic algorithm used to sign the certificate (e.g., SHA256-RSA), The algorithm used for the public key (e.g., RSA, ECC)" 
        },
        { 
          name: "Serial Num", 
          tooltip: "A unique identifier assigned to the certificate by the issuer" 
        }
      ]
    }
    ,
    {
      id: 7,
      name: "Server Finished",
      icon: <IconEncrypted className="text-orange-400" />,
      from: "backend",
      to: "client",
      fields: [
        { name: "Handshake MAC", tooltip: "Server final handshake proof" }
      ]
    },
    {
      id: 8,
      name: "Client Finished",
      icon: <IconEncrypted className="text-orange-400" />,
      from: "client",
      to: "backend",
      fields: [
        { name: "Handshake MAC", tooltip: "Client final handshake proof" }
      ]
    },
    {
      id: 9,
      name: "App Data (Client)",
      icon: <IconEncrypted className="text-orange-400" />,
      from: "client",
      to: "backend",
      fields: [
        { name: "Encrypted Payload", tooltip: "Client's protected data" }
      ]
    },
    {
      id: 10,
      name: "App Data (Server)",
      icon: <IconEncrypted className="text-orange-400" />,
      from: "backend",
      to: "client",
      fields: [
        { name: "Encrypted Payload", tooltip: "Server's protected data" }
      ]
    }
  ];

  // Calculate the total diagram height
  const calculateTotalHeight = (stepsArr, visible) => {
    if (visible.length === 0) return remToPx(20);
    const lastStepIndex = Math.max(...visible);
    let height = getStepY(lastStepIndex) + remToPx(8);

    const step = stepsArr[lastStepIndex];
    if (step?.fields?.length) {
      const linesNeeded = Math.ceil(step.fields.length / 3);
      height += linesNeeded * remToPx(4);
    }
    height += remToPx(12);
    return height;
  };

  // Watch dimension changes and rebuild on resize
  useEffect(() => {
    if (!svgRef.current) return;

    let resizeTimer;
    const handleResize = () => {
      clearTimeout(resizeTimer);
      resizeTimer = setTimeout(() => {
        // Re-draw steps already visible
        const drawn = Array.from(drawnStepsRef.current);
        d3.select(svgRef.current).selectAll("*").remove();
        drawnStepsRef.current.clear();

        const diagram = setupInitialDiagram();
        diagramRef.current = diagram;

        // Re-draw previously visible
        drawn.forEach((idx) => {
          if (steps[idx]) {
            drawStep(steps[idx], idx);
            drawnStepsRef.current.add(idx);
          }
        });

        const newH = calculateTotalHeight(steps, visibleSteps);
        updateDiagramHeight(newH);
      }, 150);
    };

    const diagram = setupInitialDiagram();
    diagramRef.current = diagram;

    // Draw all newly visible steps
    visibleSteps.forEach((i) => {
      if (!drawnStepsRef.current.has(i)) {
        if (steps[i]) {
          drawStep(steps[i], i);
          drawnStepsRef.current.add(i);
        }
      }
    });

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
      if (resizeTimer) clearTimeout(resizeTimer);
    };
    // eslint-disable-next-line
  }, [currentStep, visibleSteps]);

  // Initialize the diagram w/ 4 vertical lines
  const setupInitialDiagram = () => {
    if (currentStep === -1 || visibleSteps.length === 0) {
      d3.select(svgRef.current).selectAll("*").remove();
      drawnStepsRef.current.clear();
      setTotalHeight(remToPx(20));
      return;
    }

    const svg = d3.select(svgRef.current);
    const width = svgRef.current.clientWidth;
    const newH = calculateTotalHeight(steps, visibleSteps);

    svg.attr("height", newH);
    setTotalHeight(newH);

    // Marker (arrowhead) definition
    if (!svg.select("defs").size()) {
      const defs = svg.append("defs");
      defs
        .append("marker")
        .attr("id", "arrowhead")
        .attr("markerWidth", remToPx(2))
        .attr("markerHeight", remToPx(2))
        .attr("refX", remToPx(0.3))
        .attr("refY", remToPx(1))
        .attr("orient", "auto")
        .attr("markerUnits", "strokeWidth")
        .append("path")
        .attr("d", `M0,0 L0,${remToPx(2)} L${remToPx(1.6)},${remToPx(1)} z`)
        .style("fill", "#f97316");
    }

    // Distribute four vertical lines: (Client, DNS, CFS, Backend)
    //  10%, 30%, 60%, 90% of width
    const isSmall = width < 768;
    const xClient = isSmall ? 0.10 * width : 0.10 * width;
    const xDNS = isSmall ? 0.30 * width : 0.30 * width;
    const xCFS = isSmall ? 0.60 * width : 0.60 * width;
    const xBackend = isSmall ? 0.90 * width : 0.90 * width;

    // Top banner
    const banner = svg.append("g").attr("class", "top-banner");
    banner
      .append("rect")
      .attr("x", 0)
      .attr("y", 0)
      .attr("width", width)
      .attr("height", topBannerHeight)
      .style("fill", "#1E293B");

    // Column labels
    banner
      .append("text")
      .attr("x", xClient)
      .attr("y", topBannerHeight / 2)
      .attr("text-anchor", "middle")
      .attr("dominant-baseline", "middle")
      .style("fill", "#ffffff")
      .style("font-size", globalFontSizes.c3.flowTitle)
      .style("font-family", "'Inter', sans-serif")
      .text("Client");

    banner
      .append("text")
      .attr("x", xDNS)
      .attr("y", topBannerHeight / 2)
      .attr("text-anchor", "middle")
      .attr("dominant-baseline", "middle")
      .style("fill", "#ffffff")
      .style("font-size", globalFontSizes.c3.flowTitle)
      .style("font-family", "'Inter', sans-serif")
      .text("DNS");

    banner
      .append("text")
      .attr("x", xCFS)
      .attr("y", topBannerHeight / 2)
      .attr("text-anchor", "middle")
      .attr("dominant-baseline", "middle")
      .style("fill", "#ffffff")
      .style("font-size", globalFontSizes.c3.flowTitle)
      .style("font-family", "'Inter', sans-serif")
      .text("Client-Facing Server");

    banner
      .append("text")
      .attr("x", xBackend)
      .attr("y", topBannerHeight / 2)
      .attr("text-anchor", "middle")
      .attr("dominant-baseline", "middle")
      .style("fill", "#ffffff")
      .style("font-size", globalFontSizes.c3.flowTitle)
      .style("font-family", "'Inter', sans-serif")
      .text("Backend Server");

    function drawLine(selector, xCoord) {
      const ln = svg.select(selector);
      if (ln.empty()) {
        svg
          .append("line")
          .attr("class", selector.substring(1))
          .attr("x1", xCoord)
          .attr("y1", topBannerHeight)
          .attr("x2", xCoord)
          .attr("y2", newH)
          .style("stroke", "#3B82F6")
          .style("stroke-width", 2);
      } else {
        ln.attr("x1", xCoord).attr("x2", xCoord).attr("y2", newH);
      }
    }

    drawLine(".line-client", xClient);
    drawLine(".line-dns", xDNS);
    drawLine(".line-cfs", xCFS);
    drawLine(".line-backend", xBackend);

    return {
      svg,
      clientX: xClient,
      dnsX: xDNS,
      cfsX: xCFS,
      backendX: xBackend,
      width,
      height: newH
    };
  };

  // Update height & vertical lines
  const updateDiagramHeight = (newH) => {
    if (!svgRef.current) return;
    d3.select(svgRef.current).attr("height", newH);
    setTotalHeight(newH);

    const { svg } = diagramRef.current || {};
    if (!svg) return;
    svg.select(".line-client").attr("y2", newH);
    svg.select(".line-dns").attr("y2", newH);
    svg.select(".line-cfs").attr("y2", newH);
    svg.select(".line-backend").attr("y2", newH);
  };

  // Convert an icon React element to raw HTML
  const renderIconToString = (iconJsx) => {
    return ReactDOMServer.renderToString(iconJsx);
  };

  // Draw a single step
  const drawStep = (step, index) => {
    const { svg, clientX, dnsX, cfsX, backendX } = diagramRef.current;
    if (!svg || !step) return;

    // pick fromX, toX based on "from" and "to"
    let fromX = clientX;
    if (step.from === "dns") fromX = dnsX;
    if (step.from === "cfs") fromX = cfsX;
    if (step.from === "backend") fromX = backendX;

    let toX = clientX;
    if (step.to === "dns") toX = dnsX;
    if (step.to === "cfs") toX = cfsX;
    if (step.to === "backend") toX = backendX;

    const y = getStepY(index);

    // Group
    const group = svg.append("g").attr("class", `step-${index}`);

    // 1) Title + Icon above line
    const titleY = y - remToPx(2);
    const labelGroup = group
      .append("g")
      .attr("transform", `translate(${(fromX + toX) / 2}, ${titleY})`);

    const flowTitleG = labelGroup.append("g").attr("class", "flow-title-group");

    const textEl = flowTitleG
      .append("text")
      .attr("text-anchor", "start")
      .attr("dominant-baseline", "middle")
      .style("font-family", "'Inter', sans-serif")
      .style("fill", "#ffffff")
      .style("font-size", globalFontSizes.c3.flowTitle)
      .style("font-weight", 600)
      .text(step.name);

    const textWidth = textEl.node()?.getComputedTextLength() || 0;
    const iconSize = remToPx(5);
    const margin = 10;
    let iconHTML = step.icon ? renderIconToString(step.icon) : "";

    const totalTitleWidth = (iconHTML ? iconSize + margin : 0) + textWidth;
    flowTitleG.attr("transform", `translate(${-totalTitleWidth / 2}, 0)`);

    if (iconHTML) {
      flowTitleG
        .append("foreignObject")
        .attr("x", 0)
        .attr("y", -iconSize / 2)
        .attr("width", iconSize)
        .attr("height", iconSize)
        .html(() => iconHTML);
    }

    textEl.attr("x", iconHTML ? iconSize + margin : 0).attr("y", 0);

    // 2) Dotted arrow line
    const lineEl = group
      .append("line")
      .attr("x1", fromX)
      .attr("y1", y)
      .attr("x2", fromX)
      .attr("y2", y)
      .style("stroke", "#ffffff")
      .style("stroke-width", 2)
      .style("stroke-dasharray", "5,5")
      .attr("marker-end", "url(#arrowhead)");

    lineEl
      .transition()
      .duration(800)
      .attr("x2", toX > fromX ? toX - 12 : toX + 12);

    // 3) Fields below the line
    const fieldsGroup = group
      .append("g")
      .attr("transform", `translate(${(fromX + toX) / 2}, ${y + remToPx(2)})`);

    const allFieldsText = (step.fields || []).map((f) => f.name).join(" | ");
    // measure to see if we must wrap
    const temp = fieldsGroup
      .append("text")
      .style("font-size", globalFontSizes.c3.flowField)
      .text(allFieldsText);
    const totalWidth = temp.node()?.getComputedTextLength() || 0;
    temp.remove();

    const available = Math.abs(toX - fromX);
    const linesNeeded = step.fields ? Math.ceil(totalWidth / available) : 1;
    const fieldsPerLine = step.fields ? Math.ceil(step.fields.length / linesNeeded) : 1;

    // break fields
    const lines = [];
    if (step.fields) {
      for (let i = 0; i < step.fields.length; i += fieldsPerLine) {
        lines.push(step.fields.slice(i, i + fieldsPerLine));
      }
    }

    lines.forEach((lineFields, idxLine) => {
      // Construct the text for that line
      const lineTxt = lineFields.map((f) => f.name).join(" | ");
      const textE = fieldsGroup
        .append("text")
        .attr("x", 0)
        .attr("y", idxLine * remToPx(3))
        .attr("text-anchor", "middle")
        .attr("dominant-baseline", "middle")
        .style("font-family", "'Inter', sans-serif")
        .style("font-size", globalFontSizes.c3.flowField)
        .style("fill", "#60A5FA") // default color
        .style("opacity", 0)
        .text(lineTxt);

      // Fade in
      textE.transition().duration(800).style("opacity", 1);

      // position for each field's hover
      let currentX = -(textE.node()?.getComputedTextLength() / 2);

      lineFields.forEach((field, iF) => {
        // The text for this single field + " | " except for the last field
        const fieldStr = field.name + (iF < lineFields.length - 1 ? " | " : "");

        // We'll measure just this field portion
        const tmpMeas = fieldsGroup
          .append("text")
          .style("font-size", globalFontSizes.c3.flowField)
          .text(fieldStr);
        const w = tmpMeas.node()?.getComputedTextLength() || 0;
        tmpMeas.remove();

        // Transparent rect for hover area
        const rectField = fieldsGroup
          .append("rect")
          .attr("x", currentX)
          .attr("y", idxLine * remToPx(3) - remToPx(2.1))
          .attr("width", w)
          .attr("height", remToPx(3))
          .attr("fill", "transparent")
          .style("cursor", "pointer");

        // If field has a specialColor, apply it
        if (field.specialColor) {
          // We'll do a quick step:
          // Re-render just this field portion in a different color
          // The simplest approach: we do a small timeout after the text is drawn
          // to replace that substring with special color, or we do a global approach:
          setTimeout(() => {
            // Find the big line's text node
            const bigLine = textE.node();
            if (!bigLine) return;
            const original = bigLine.textContent;
            // We'll replace the chunk for this field with a marker
            let replaced = original.replace(field.name, `\u0001${field.name}\u0002`);
            bigLine.textContent = "";

            // Re-inject in segments with <tspan>
            const segments = replaced.split(/(\u0001|\u0002)/);
            segments.forEach((seg) => {
              if (seg === "\u0001") {
                // start color
                return;
              }
              if (seg === "\u0002") {
                // end color
                return;
              }
              // If we are inside color region
              const isColored = replaced.indexOf("\u0001") < replaced.indexOf(seg) &&
                                replaced.indexOf(seg) < replaced.indexOf("\u0002");

              bigLine.appendChild(
                // create a tspan
                (() => {
                  const tspan = document.createElementNS("http://www.w3.org/2000/svg", "tspan");
                  tspan.setAttribute("style", isColored 
                    ? `fill: ${field.specialColor}; font-family: 'Inter'; font-weight: 600;`
                    : `fill: #60A5FA; font-family: 'Inter'; font-weight: 600;`
                  );
                  tspan.textContent = seg;
                  return tspan;
                })()
              );
            });
          }, 50);
        }

        // Tooltip logic
        rectField
          .on("mouseover", function () {
            // remove old
            svg.selectAll(".field-tooltip").remove();
            const tooltip = svg.append("g").attr("class", "field-tooltip").style("opacity", 0);

            const tooltipTxt = tooltip
              .append("text")
              .attr("text-anchor", "start")
              .style("font-size", globalFontSizes.c3.flowTooltip)
              .style("fill", "#f97316")
              .style("font-family", "'Inter', sans-serif");

            const linesArr = field.tooltip.split("\n").filter((l) => l.trim() !== "");
            const lineH = remToPx(3);
            const maxW = available * 0.8;

            // Very basic word-wrap
            const wrap = (txt) => {
              const measure = tooltip
                .append("text")
                .style("font-size", globalFontSizes.c3.flowTooltip)
                .text(txt);
              const measureW = measure.node()?.getComputedTextLength() || 0;
              measure.remove();
              if (measureW <= maxW) return [txt];

              // break text
              const words = txt.split(" ");
              let partial = "";
              const result = [];
              words.forEach((w2) => {
                const attempt = partial ? partial + " " + w2 : w2;
                const test = tooltip
                  .append("text")
                  .style("font-size", globalFontSizes.c3.flowTooltip)
                  .text(attempt);
                const tW = test.node()?.getComputedTextLength() || 0;
                test.remove();
                if (tW > maxW && partial) {
                  result.push(partial);
                  partial = w2;
                } else {
                  partial = attempt;
                }
              });
              if (partial) result.push(partial);
              return result;
            };

            let offsetY = 0;
            linesArr.forEach((ln) => {
              const wrapped = wrap(ln);
              wrapped.forEach((subL) => {
                tooltipTxt
                  .append("tspan")
                  .attr("x", 0)
                  .attr("y", offsetY)
                  .text(subL);
                offsetY += lineH;
              });
            });

            const bb = tooltipTxt.node()?.getBBox() || { x: 0, y: 0, width: 0, height: 0 };
            tooltip
              .insert("rect", "text")
              .attr("rx", remToPx(1))
              .attr("fill", "#111827")
              .attr("x", bb.x - remToPx(1))
              .attr("y", bb.y - remToPx(0.5))
              .attr("width", bb.width + remToPx(2))
              .attr("height", bb.height + remToPx(1))
              .style("filter", "drop-shadow(0 4px 6px rgba(0,0,0,0.4))");

            const tooltipY = y + remToPx(8) + idxLine * remToPx(3);
            const tooltipX = ((fromX + toX) / 2) - bb.width / 2;
            tooltip
              .attr("transform", `translate(${tooltipX}, ${tooltipY})`)
              .transition()
              .duration(200)
              .style("opacity", 1);
          })
          .on("mouseout", () => {
            svg
              .selectAll(".field-tooltip")
              .transition()
              .duration(200)
              .style("opacity", 0)
              .remove();
          });

        currentX += w;
      });
    });

    // 4) If "App Data", animate circles
    if (step.name.startsWith("App Data")) {
      const circlesGroup = group.append("g").attr("class", `circles-${index}`);
      const interval = setInterval(() => {
        circlesGroup
          .append("circle")
          .attr("cx", fromX)
          .attr("cy", y)
          .attr("r", remToPx(0.9))
          .attr("fill", "#FBBF24")
          .transition()
          .duration(2000)
          .ease(d3.easeLinear)
          .attr("cx", toX > fromX ? toX - 18 : toX + 18)
          .remove();
      }, 800);

      // Clear if re-rendered
      return () => clearInterval(interval);
    }
  };

  return (
    <div className="relative w-full h-full overflow-y-auto scrollbar-none">
      <div
        className="w-full"
        style={{
          minHeight: '100vh',
          height: totalHeight ? `${totalHeight}px` : '100vh'
        }}
      >
        <svg
          ref={svgRef}
          className="w-full h-full"
          style={{ minHeight: totalHeight }}
          preserveAspectRatio="xMidYMin meet"
        />
      </div>
    </div>
  );
};

export default C3Diagram;